.block-center {
    margin: 30px 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-fiolett {
    display: inline-flex;
    padding: 20px 30px;
    color: #fff;
    justify-content: center;
    align-items: center;
    background: #3F47CC;
    border-radius: 4px;
    transition: all .3s;
    text-decoration: none;

    &:hover {
        background: lighten(#3F47CC, 5%)
    }
}

.approve {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    &__item {
        margin: 20px;
        border: 1px solid #ececec;
        background: #fff;
        padding: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-basis: 50%;
        max-width: 300px;
        border-radius: 10%;
        
        position: relative;
        z-index: 100;

    }

    &__number {
        font-size: 50px;
        color: #DDE1E6;
        margin-bottom: 20px;
        font-weight: 600;
    }

    &__img {
        margin-bottom: 30px;
    }

    &__txt {
        color: #18191E;
        line-height: 1.3;
        text-align: center;
        font-size: 13px;
    }

    &__link {
        color: #0055CC;
    }

    &__title {
        text-align: left;
        font-weight: 600;
        margin-bottom: 10px;
    }

    &__descr {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-size: 13px;
        color: #66788F;
        margin-bottom: 15px;

        p {
            padding: 3px 0;
            margin: 0;
            line-height: 1;
        }
    }

    &__rectangle {
        width: 30px;
        height: 20px;
        border-radius: 4px;
        background: #E06800;
        margin-right: 10px;
    }
}

@media only screen and (max-width : 768px) {
    .approve {

        &__item {
            flex-basis: 100%;
            max-width: 100%''
        }
    }
}
@media only screen and (max-width : 576px) {
       .approve {

        &__item {
            padding: 20px;
        }
    }
    
}